'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Card, CardContent } from '@/components/ui/card'
import { Calendar } from '@/components/ui/calendar'
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover'
import { CalendarIcon, Search } from 'lucide-react'
import { format } from 'date-fns'
import { cn } from '@/lib/utils'

interface ProfileData {
  name: string
  totalVideos: number
  estimatedSize: string
}

export default function Home() {
  const [profileLink, setProfileLink] = useState('')
  const [batchLinks, setBatchLinks] = useState('')
  const [startDate, setStartDate] = useState<Date>()
  const [endDate, setEndDate] = useState<Date>()
  const [profileData, setProfileData] = useState<ProfileData | null>(null)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')

  const handlePreview = async () => {
    if (!profileLink.trim()) {
      setError('Please enter a TikTok profile link')
      return
    }

    if (!startDate || !endDate) {
      setError('Please select start and end dates first')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      const response = await fetch('/api/profile', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          profileLink,
          startDate: startDate.toISOString(),
          endDate: endDate.toISOString(),
        }),
      })

      const data = await response.json()

      if (response.ok && data.success) {
        setProfileData(data.data)
      } else {
        setError(data.error || 'Failed to fetch profile data')
      }
    } catch (err) {
      setError('Network error. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleDownload = async () => {
    if (!profileData) return

    setIsLoading(true)
    try {
      const response = await fetch('/api/download', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          profileLink,
          startDate: startDate?.toISOString(),
          endDate: endDate?.toISOString(),
        }),
      })

      if (response.ok) {
        const blob = await response.blob()
        const url = window.URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `tiktok-videos-${profileData.name}.zip`
        document.body.appendChild(a)
        a.click()
        window.URL.revokeObjectURL(url)
        document.body.removeChild(a)
      } else {
        const data = await response.json()
        setError(data.error || 'Download failed')
      }
    } catch (err) {
      setError('Download failed. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  const handleBatchDownload = async () => {
    if (!batchLinks.trim()) {
      setError('Please enter TikTok video links for batch download')
      return
    }

    setIsLoading(true)
    setError('')

    try {
      const links = batchLinks.split('\n').filter(link => link.trim())
      
      const response = await fetch('/api/batch-download', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ links }),
      })

      if (response.ok) {
        const blob = await response.blob()
        const url = window.URL.createObjectURL(blob)
        const a = document.createElement('a')
        a.href = url
        a.download = `tiktok-batch-videos.zip`
        document.body.appendChild(a)
        a.click()
        window.URL.revokeObjectURL(url)
        document.body.removeChild(a)
      } else {
        const data = await response.json()
        setError(data.error || 'Batch download failed')
      }
    } catch (err) {
      setError('Batch download failed. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#193c76] via-[#2563eb] to-[#3b82f6]">
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="text-center mb-12">
          <div className="flex items-center justify-between mb-6">
            <div></div>
            <h1 className="text-6xl font-bold text-white tracking-wider">
              
            </h1>
            <div className="text-white text-sm">
              <div className="w-8 h-8 bg-white/20 rounded flex items-center justify-center">
                <span className="text-xs font-bold">M</span>
              </div>
            </div>
          </div>
          <p className="text-white text-xl mb-2">Unstoppable Creativity | FYP FYP FYP</p>
          <p className="text-white/90 text-lg">Paste your profile link and download your content ✨</p>
        </div>

        {/* Profile Link Input */}
        <div className="max-w-4xl mx-auto mb-8">
          <div className="relative">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <Search className="h-5 w-5 text-gray-400" />
            </div>
            <Input
              type="text"
              placeholder="Insert Your TikTok Profile Link..."
              value={profileLink}
              onChange={(e) => setProfileLink(e.target.value)}
              className="pl-10 pr-32 py-5 text-lg rounded-full bg-white/95 border-0 shadow-lg h-16"
            />
            <Button
              onClick={handlePreview}
              disabled={isLoading}
              className="absolute right-3 top-1/2 transform -translate-y-1/2 rounded-lg bg-[#193c76] hover:bg-[#2563eb] px-8 py-3 text-white font-semibold transition-colors"
            >
              {isLoading ? 'Loading...' : 'Preview'}
            </Button>
          </div>
        </div>

        {/* Batch Download Section */}
        <div className="max-w-4xl mx-auto mb-8">
          <Textarea
            placeholder="Paste multiple TikTok video links (satu per baris)..."
            value={batchLinks}
            onChange={(e) => setBatchLinks(e.target.value)}
            className="min-h-32 bg-white/95 border-0 shadow-lg rounded-2xl resize-none text-gray-600"
          />
          <div className="mt-4">
            <Button
              onClick={handleBatchDownload}
              disabled={isLoading}
              className="bg-[#193c76] hover:bg-[#2563eb] rounded-lg px-8 py-3 text-white font-semibold transition-colors"
            >
              {isLoading ? 'Processing...' : 'Download Batch'}
            </Button>
          </div>
        </div>

        {/* Profile Information Card */}
        <div className="max-w-4xl mx-auto">
          <Card className="bg-white/95 shadow-xl rounded-3xl border-0">
            <CardContent className="p-8">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Left Column */}
                <div className="space-y-6">
                  <div>
                    <label className="block text-gray-700 font-semibold mb-2">Profile Name:</label>
                    <Input
                      value={profileData?.name || ''}
                      readOnly
                      className="bg-gray-100 border-gray-200"
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 font-semibold mb-2">Total Video:</label>
                    <Input
                      value={profileData?.totalVideos || ''}
                      readOnly
                      className="bg-gray-100 border-gray-200"
                    />
                  </div>
                  <div>
                    <label className="block text-gray-700 font-semibold mb-2">Estimated Size:</label>
                    <Input
                      value={profileData?.estimatedSize || ''}
                      readOnly
                      className="bg-gray-100 border-gray-200"
                    />
                  </div>
                </div>

                {/* Right Column */}
                <div className="space-y-6">
                  <div>
                    <label className="block text-gray-700 font-semibold mb-2">Start Date:</label>
                    <Popover>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          className={cn(
                            "w-full justify-start text-left font-normal",
                            !startDate && "text-muted-foreground"
                          )}
                        >
                          <CalendarIcon className="mr-2 h-4 w-4" />
                          {startDate ? format(startDate, "dd/MM/yyyy") : "dd/mm/yyyy"}
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto p-0">
                        <Calendar
                          mode="single"
                          selected={startDate}
                          onSelect={setStartDate}
                          initialFocus
                        />
                      </PopoverContent>
                    </Popover>
                  </div>
                  <div>
                    <label className="block text-gray-700 font-semibold mb-2">End Date:</label>
                    <Popover>
                      <PopoverTrigger asChild>
                        <Button
                          variant="outline"
                          className={cn(
                            "w-full justify-start text-left font-normal",
                            !endDate && "text-muted-foreground"
                          )}
                        >
                          <CalendarIcon className="mr-2 h-4 w-4" />
                          {endDate ? format(endDate, "dd/MM/yyyy") : "dd/mm/yyyy"}
                        </Button>
                      </PopoverTrigger>
                      <PopoverContent className="w-auto p-0">
                        <Calendar
                          mode="single"
                          selected={endDate}
                          onSelect={setEndDate}
                          initialFocus
                        />
                      </PopoverContent>
                    </Popover>
                  </div>
                  <div className="pt-4">
                    <Button
                      onClick={handleDownload}
                      disabled={isLoading || !profileData}
                      className="w-full bg-blue-600 hover:bg-blue-700 rounded-full py-3 text-white font-semibold"
                    >
                      {isLoading ? 'Downloading...' : 'Download'}
                    </Button>
                  </div>
                </div>
              </div>

              {/* Error Message */}
              {error && (
                <div className="mt-6 p-4 bg-red-100 border border-red-400 text-red-700 rounded-lg">
                  {error}
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
